$(document).ready(function() {
  //   radio button
  $(".radioButtons__input:eq(0)").attr("checked", true);

  //  radio buttons
  const allRadio = $(".radioButtons__input");
  //    radio button
  allRadio.on("change", function() {
    //  radio buttons    checked    active
    allRadio.map(function() {
      $(this).prop("checked")
        ? $(this)
            .parent()
            .addClass("active")
        : $(this)
            .parent()
            .removeClass("active");
    });
  });
});
